import React from "react";
import { setRecipeInfo } from "./actions";
import { actionTypes } from "../index";

export const initialState = {
  title: "Microwaved Potato",
  img:
    "data: image/jpeg;base64,/9j / 4AAQSkZJRgABAQAAAQABAAD / 2wCEAAkGBxITEhUTExMWFhUXGB4ZFxgXGRcXGhoaGhgaGhoYGxgZHiggGhslGxcXIjEiJSkrLi4uGB8zODMtNygtLisBCgoKDg0OGhAQGy0lICUtLS0uLS0tLS0tLS8tLS0tLS0tLS0tLS0tLS0tLS0tLS0tLS0tLS0tLS0tLS0tLS0tLf / AABEIAMEBBQMBIgACEQEDEQH / ",
  prepTime: "10 min",
  totalTime: "10 min",
  amount: "1 potato",
  desc: "The easiest way to enjoy a potato.",
  ingredients: ["1 large potato"],
  instructions: [
    "Wash potato",
    "Prick holes in potato with a fork",
    "Microwave for 5 mins, then flip the potato, and microwave for another 5 mins",
    "Eat"
  ]
};

export const reducer = (state, action) => {
  switch (action.type) {
    case actionTypes.SET_RECIPE:
      return setRecipeInfo(action.recipeName, action.category);
    default:
      return state;
  }
};
